<template>
  <div class="full-screen christmas-draw">
    <div class="turntable">
      <div class="draw-btn" @click="drawBtnClick"></div>
      <ul class="draw-ul">
        <li class="draw-li" v-for="(item, i) in 8" :key="item" :class="{'on': i == index}"></li>
      </ul>
    </div>
    <!-- note-dialog -->
    <pem-dia-log type="modal" ref="noteDialog" :showCloseBtn="false">
      <div class="note-wrapper">
        <div class="close-btn" @click="noteDialogHide"></div>
        <h4 class="note-title"></h4>
        <p class="note-text" v-html="noteText"></p>
        <p class="note-btn" @click="noteDialogSure"></p>
      </div>
    </pem-dia-log>
    <!-- draw-dialog -->
    <pem-dia-log type="modal" ref="drawDialog" :showCloseBtn="false">
      <div class="draw-wrapper">
        <div class="draw-logo"></div>
        <h4 class="draw-title"></h4>
        <p class="draw-text1" v-html="drawText1"></p>
        <p v-if="drawText2" class="draw-text2" v-html="drawText2"></p>
        <div class="draw-btnwrap">
          <div class="draw-btn" @click="drawDialogHide"></div>
        </div>
      </div>
    </pem-dia-log>
    <!-- toast -->
    <pem-toast ref="toastEle" :text="toastText"></pem-toast>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemToast from 'components/pem-toast/pem-toast'
import PemDiaLog from 'components/dialog/dialog'
import { MiniSiteApi } from 'api'
import { setTitle } from 'common/js/util'
import storage from 'common/js/storage.js'

export default {
  data () {
    return {
      sdphone: storage.get('sdphone') || '',
      needLogin: false,
      toastText: '',
      drawText1: '',
      drawText2: '',
      drawId: -1,
      drawOrder: 1,
      noteType: -1,
      noteText: '',
      index: -1, // 
      count: 8, // 
      timer: 0, // 
      speed: 200, // 
      times: 0, // 
      cycle: 3, // 
      prize: -1, // 
      click: true
    }
  },
  components: {
    PemScrollView,
    PemToast,
    PemDiaLog
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      setTitle('')
      this.wxShare({
        title: '',
        content: ' ',
        imgUrl: 'http://cdn.pemarket.com.cn/operation/christmas-share-icon.png',
        linkUrl: `${window.location.origin}/christmas-activity`
      })
      if (this.sdphone) {
        this.getDrawStatus()
      }
    },
    drawBtnClick () {
      if (+this.noteType !== -14 || this.drawId > -1) {
        this.noteDialogShow()
        return
      }
      this.goDraw()
      this.startLottery()
    },
    getDrawStatus () {
      if (!this.sdphone) return
      this._getDrawStatus({'mobile': this.sdphone}, (data) => {
        this.noteType = +(data && data.resultcode) || 0
      })
    },
    goDraw () {
      if (!this.sdphone) return
      this._goDraw({'mobile': this.sdphone}, (data) => {
        let tempcode = +(data && data.resultcode) || 0
        if (tempcode === -14) {
          // 
          this.drawId = +(data && data.award) || 7
          this.drawOrder = +(data && data.order) || 1
          this.prize = this.drawId - 1
          this.noteType = -15
        } else {
          // 
          this.index = -1
          this.noteType = +(data && data.resultcode) || 0
          this.noteDialogShow()
        }
      })
    },
    // 
    startLottery () {
      if (!this.click) {
        return
      }
      this.startRoll()
    },
    startRoll () {
      this.times += 1 // 
      this.oneRoll() // ,
      //  && 
      if (this.times > this.cycle + 10 && this.prize === this.index) {
        clearTimeout(this.timer) // ,
        this.prize = -1
        this.times = 0
        this.speed = 200
        this.click = true
        let that = this
        if (this.drawId > -1) {
          setTimeout(() => {
            that.drawDialogShow()
          }, 500)
        }
      } else {
        if (this.times < this.cycle) {
          this.speed -= 10 // 
        } else if (this.times > this.cycle + 10 && ((this.prize === 0 && this.index === 7) || this.prize === this.index + 1)) {
          this.speed += 110
        } else {
          this.speed += 20
        }
        if (this.speed < 40) {
          this.speed = 40
        }
        this.timer = setTimeout(this.startRoll, this.speed)
      }
    },
    // 
    oneRoll () {
      // 
      let index = this.index
      const count = this.count // 
      index += 1
      if (index > count - 1) {
        index = 0
      }
      this.index = index
    },
    drawDialogShow () {
      if (+this.drawId === 7) {
        this.drawText1 = ''
        this.drawText2 = ''
      } else {
        let award = {
          '1': '',
          '2': '3',
          '3': '',
          '4': '1',
          '5': '1000',
          '6': '7',
          '8': '2'
        }[this.drawId]
        this.drawText1 = `${award}~`
        this.drawText2 = (+this.drawOrder === 1) ? '2' : ''
      }
      this.$refs.drawDialog.show()
    },
    drawDialogHide () {
      this.$refs.drawDialog.hide()
    },
    noteDialogShow () {
      this.noteText = {
        '-1': '~',
        '0': ',!',
        '-11': '~',
        '-12': '~',
        '-13': '~',
        '-15': '~'
      }[this.noteType]
      if (!this.noteText) return
      this.$refs.noteDialog.show()
    },
    noteDialogHide () {
      this.$refs.noteDialog.hide()
    },
    noteDialogSure () {
      if (+this.noteType === -1) {
        window.location.href = `${window.location.origin}/christmas-activity`
        return
      }
      this.$refs.noteDialog.hide()
    },
    showToast (txt) {
      if (!txt) return
      this.toastText = txt
      this.$refs.toastEle.show()
    },
    hideToast () {
      this.$refs.toastEle.hide()
    },
    _getDrawStatus (options, callback) {
      MiniSiteApi.getDrawStatus(options).then(data => {
        callback(data)
      })
    },
    _goDraw (options, callback) {
      MiniSiteApi.goDraw(options).then(data => {
        callback(data)
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  @keyframes btnAnimate
    0%
      -webkit-transform:scale(1);
      transform:scale(1)
    to
      -webkit-transform:scale(1.03);
      transform:scale(1.03)
  .christmas-draw
    font-size:0;
    background: url('./images/bg@2x.png'), url('./images/bg2@2x.png')
    background-size: 100% auto;
    background-repeat: no-repeat, repeat;
    background-position: left top, left 12.1rem
    background-color: #9B0507;
    max-width: 750px;
    margin: 0 auto;
    min-height: 12.12rem
    .turntable
      position: relative
      width: 7.5rem
      height: 5.76rem
      margin-top: 5.80rem
      bg-image('./images/turntable')
      background-size: 100% 100%
      .draw-btn
        position: absolute
        z-index: 5
        top: 1.80rem
        left: 50%
        margin-left: -1.05rem
        width: 2.10rem
        height: 1.32rem
        background-size: 100% 100%
        background-repeat: no-repeat
        bg-image('./images/btn-draw')
        animation: btnAnimate .4s infinite alternate forwards;
      .draw-ul
        position: absolute
        left: 0.63rem
        top: 0.48rem
        width: 6.24rem
        height: 3.92rem
        .draw-li
          position: absolute
          width: 2.04rem
          height: 1.26rem
          border-radius: 0.04rem
          &.on
            background: rgba(209,54,56,0.5);
            // background: red
          &:nth-child(1)
            left: 0
            top: 0
          &:nth-child(2)
            left: 2.08rem
            top: 0
          &:nth-child(3)
            right: 0
            top: 0
          &:nth-child(4)
            right: 0
            top: 50%
            margin-top: -0.63rem
          &:nth-child(5)
            right: 0
            bottom: 0
          &:nth-child(6)
            left: 50%
            margin-left: -1rem
            bottom: 0
          &:nth-child(7)
            left: 0
            bottom: 0
          &:nth-child(8)
            left: 0
            top: 50%
            margin-top: -0.63rem
    .note-wrapper
      box-sizing: border-box
      width: 295px
      background-color: #ffffff
      padding: 32px 35px 46px
      text-align: center
      border-radius: 8px;
      .note-title
        font-size: 15px
        font-weight: bold
        color: #202020;
      .note-text
        font-size: 13px
        color: #333333
        line-height: 20px 
        padding: 25px 0
      .note-btn
        position: absolute
        left: 0
        bottom: 0
        width: 100%
        height: 45px
        line-height: 45px
        font-size: 15px;
        color: #FFC131;
        border-top: 0.5px solid #DBDBDB;
      .close-btn
        position: absolute
        top: 0
        right: 0
        width: 42px
        height: 42px
        bg-image('./images/close')
        background-size: 12px 12px
        background-repeat: no-repeat
        background-position: center
    .draw-wrapper
      box-sizing: border-box
      width: 295px
      min-height: 276px
      bg-image('./images/draw-bg')
      background-size: 100% 100%
      background-color: #D7261F
      padding: 105px 0 15px
      text-align: center
      border-radius: 8px;
      .draw-logo
        position: absolute
        top: 0
        left: 50%
        margin-left: -52px
        width: 104px
        height: 104px
        bg-image('./images/draw-logo')
        background-size: 100% 100%
      .draw-title
        font-family: PingFangSC-Medium;
        font-size: 18px
        font-weight: bold
        color: #FFFAE7;
        line-height 25px
        padding-bottom: 15px
      .draw-text1, .draw-text2
        font-family: PingFangSC-Regular;
        font-size: 14px;
        color: #FFFFFF;
        line-height: 20px
      .draw-text1
        padding: 0 28px 
      .draw-text2
        padding-top: 8px
      .draw-btnwrap
        padding-top: 12px
      .draw-btn
        width: 138px
        height: 54px
        margin: 0 auto
        bg-image('./images/draw-btn')
        background-size: 100% 100%
</style>



